// Sidebar.js
import React, { useEffect } from 'react';
import { Link } from '@reach/router';

import routes from '../routes';
import './Sidebar.css';

const Sidebar = () => {

  // Function to dynamically adjust sidebar height to match the card height
    const matchSidebarHeight = () => {
        const card = document.querySelector('.card');
        const sidebar = document.querySelector('.sidebar');

        if (card && sidebar) {
            const cardHeight = card.offsetHeight;
            sidebar.style.height = `${cardHeight}px`;
        }
    };

    // Use useEffect to trigger the function after mounting
    useEffect(() => {
        matchSidebarHeight(); // Initial adjustment
        // Optional: Adjust on window resize if content resizes dynamically
        window.addEventListener('resize', matchSidebarHeight);
        return () => window.removeEventListener('resize', matchSidebarHeight);
    }, []);

  return (
    <div className="sidebar">
      <nav className="nav">
        {routes.map((route, index) => (
          <Link key={index} to={route.path}>
            {route.name}
          </Link>
        ))}
      </nav>
    </div>
  );
};

export default Sidebar;
